@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body {
  width: 100%;
  height: 100%;
  /* background-color: #aabfff; */
  background-color: #91acff;
}

.App {
  width: 100%;
  height: 100%;
}

.page-in-general {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
  justify-content: center;
}

.greeting {
  width: 40%;
  height: 40%;
  background-color: #ffffff;
  border: 0px solid #ffffff;
  border-radius: 30px;
  box-shadow: 0px 21px 18px -13px rgba(34, 60, 80, 0.31);
  box-sizing: content-box;
  padding-top: 10px;
  padding-bottom: 20px;
  padding-left: 20px;
  padding-right: 20px;
  margin-top: 15px;
  margin-bottom: 10px;
}

.greeting-text {
  margin: 0px;
}

.greeting-text-t {
  margin-top: 8px;
  margin-bottom: 1px;
}

.sign-in,
.log-in {
  font-weight: 550;
  font-size: 17px;
  width: -webkit-max-content;
  width: max-content;
  height: 40%;
  cursor: pointer;
  height: 40%;
  background-color: #ffffff;
  border: 0px solid #ffffff;
  border-radius: 30px;
  box-shadow: 0px 21px 18px -13px rgb(34 60 80 / 31%);
  box-sizing: content-box;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 25px;
  padding-right: 25px;
  margin-top: 15px;
  margin-bottom: 10px;
  /* margin-top: 10px;
  margin-bottom: 10px; */
}

h1,
h2,
.reg-or-log,
.texts,
.inputs-titles,
.inputs,
.reg-or-log-buttons,
.quote,
.write-activity-name,
.set-time-digits,
.set-time-letter,
.p-activity-name {
  font-family: "Raleway";
}

.reg-or-log {
  width: 30%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  align-content: center;
  margin-top: 5%;
  background-color: #ffffff;
  border: 0px solid #ffffff;
  border-radius: 30px;
  box-shadow: 0px 21px 18px -13px rgba(34, 60, 80, 0.31);
  box-sizing: content-box;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 20px;
  padding-right: 20px;
}

.texts {
  text-align: center;
  margin-top: 5px;
  margin-bottom: 5px;
  font-size: 20px;
}

.inputs-titles {
  text-align: start;
  margin-top: 5px;
  margin-bottom: 5px;
}

.inputs {
  height: 25px;
  border-radius: 7px;
  padding-left: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
  margin-top: 5px;
  margin-bottom: 5px;
  border: 1px solid #91acff;
}

.reg-or-log-buttons {
  width: -webkit-max-content;
  width: max-content;
  height: 30px;
  font-size: 15px;
  color: #ffffff;
  /* background: #ffeedb; */
  background-color: #7899ff ;
  border: 1px solid #ffeedb;
  border-radius: 30px;
  margin-top: 5px;
  margin-bottom: 5px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 6px;
  padding-bottom: 5px;
  cursor: pointer;
}

.window {
  position: fixed;
  font-family: Arial, Helvetica, sans-serif;
  inset: 0px;
  background: rgba(0, 0, 0, 0.8);
  z-index: 99999;
  transition: opacity 1s ease-in 0s;
  pointer-events: auto;
  overflow-y: auto;
}

.icon {
  width: 20px;
  margin-top: 5px;
}

.header {
  width: 100%;
  background: #ffeedb;
  display: flex;
}

.header__title {
  font-family: "Raleway";
  font-size: 45px;
  margin-top: 15px;
  margin-bottom: 15px;
  margin-right: auto;
  margin-left: auto;
}

.common {
  width: 10%;
}

.day {
  background-color: #ffffff;
  border-radius: 50%;
}

.chosen {
  /* background-color: #c0aaff; */
  background-color: #ad91ff;
}

.with-activity {
  /* background-color: #aaeaff; */
  background-color: #91e4ff;

}

.calendar-item {
  display: flex;
  width: 290px;
  height: -webkit-max-content;
  height: max-content;
  margin-bottom: 10px;
  padding-bottom: 2px;
  background-color: #ffffff;
  border-radius: 9px;
  flex-direction: column;
  align-items: center;
}


.div-activity-name {
  background-color: #ffffff;
  border-radius: 8px;
  margin-bottom: 0px;
  display: flex;
  align-items: center;
  width: 80%;
}

.p-activity-name {
  font-size: 20px;
  font-weight: 400;
  margin-top: 7px;
  margin-bottom: 7px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.calendar-conteiner {
  width: 280px;
  height: 250px;
}

.react-calendar {
  border: 0px;
}
.react-calendar__month-view__weekdays__weekday {
  /* background-color: #aabfff; */
  background-color: #91acff;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    /* display: none; <- Crashes Chrome on hover */
    -webkit-appearance: none;
    margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}
input[type=number] {
  -moz-appearance: textfield;
}
.write-activity {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-around;
}

.write-activity-name {
  width: 80%;
  height: 23px;
  padding-left: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
  font-size: 20px;
  border: 1px solid #aabfff;
  border-radius: 6px;
}

.write-activity-btn {
  width: -webkit-max-content;
  width: max-content;
  margin-right: auto;
  margin-left: auto;
  margin-top: 10px;
}

.set-time {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-around;
}

.set-time-sections {
  width: 40%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.set-time-digits {
  width: 50px;
  height: 50px;
  box-shadow: -2px 0px 9px 0px rgb(34 60 80 / 22%) inset;
  border: 0px solid #ffffff;
  border-radius: 12px;
  font-size: 40px;
  text-align: center;
}

.set-time-letters {
  margin: 0px;
  font-size: 22px;
}

.check-mark {
  width: 30px;
  height: 30px;
}



.month {
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 6px;
  width: 45%;
  margin-top: 5px;
  margin-right: auto;
  margin-bottom: 5px;
  margin-left: auto;
  text-align: center;
}

.quote {
  margin: 0px;
}

.change-month {
  width: 39%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 10px;
}

.main-div {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2%;
}

.activities {
  display: flex;
  width: 75%;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-evenly;
}
.create-btn {
  font-size: 20px;
  background-color: #ffffff;
  border-color: #ffffff;
  color: black;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.example {
  width: 150px;
  height: 150px;
  border: 1px solid black;
  border-radius: 20px;
  background-color: #ffffff;
  margin: 10px;
}

.no-background {
  /* background-color: #aabfff; */
  background-color: #91acff;
  /* border: 0px solid #aabfff; */
  border: 0px solid #91acff;
}

* {
  font-family: "Raleway";
}

.statictics {
  padding-top: 5px;
  padding-bottom: 30px;
  padding-left: 20px;
  padding-right: 20px;
}

.activity-title {
  font-size: 15px;
  display: flex;
  justify-content: center;
}

.activity-title-h1 {
  margin-top: 7px;
  margin-bottom: 8px;
} 

.progress {
  /* background-color: #fbbec6; */
  background-color: #faa6b1;
  border: 0px;
  border-radius: 30px;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-left: 10px;
  padding-right: 10px;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: 10px;
  margin-right: 10px;
}

.progress-h2 {
  margin-top: 0px;
  margin-bottom: 12px;
  margin-left: 12px;
}

.st-example {
  width: 80%;
  height: 300px;
  border: 0px solid black;
  border-radius: 3px;
  background-color: #ffffff;
  margin-left: auto;
  margin-right: auto;
}
.statictics-img {
  width: 20px;
  height: 20px;
}

.img {
  width: 676px;
  height: 300px;
}

.bar-chart {
  width: 600;
  height: 300;

  margin-top: 5;
  margin-right: 20;
  margin-bottom: 5;
  margin-left: 0;
}

.statisctics-wrapper {
  display: flex;
  justify-content: center;
}

.btn-wrapper {
  display: flex;
  width: 100%;
  height: 10px;
  justify-content: flex-start;
  align-items: center;
}

.go-back {
  height: 33px;
  background: none;
  /* border: 1px solid #aaeaff; */
  background-color: #aaeaff;
  border: 3px solid #78deff;
  /* background-color: #91e4ff; */
  /* background-color: #78deff; */
  width: 157px;
  font-size: 15px;
  font-weight: 500;
  border-radius: 5px;
  margin-left: 20px;
  cursor: pointer;
}

